import {AbstractRouteConfiguration} from "../routing/abstract-route-configuration";
import { ActivatedRoute, Router } from '@angular/router';
import * as _ from 'underscore';
import {IEntitySelector} from "../../interface/ui/i-entity-selector";

export interface IEntityResolverConfiguration {
  keepQueryParams: boolean;
}

export abstract class AbstractEntityResolver<TRouteParams, TModel> {

  public abstract resolve(
    activatedRoute: ActivatedRoute,
    model: TModel,
    entitySelector: IEntitySelector<TModel>
  ): Promise<boolean>;

  protected constructor(
    protected readonly router: Router,
    protected readonly routeConfiguration: AbstractRouteConfiguration<TRouteParams>,
    protected readonly configuration?: IEntityResolverConfiguration,
  ) {
    this.configuration = configuration || AbstractEntityResolver.generateDefaultConfiguration();
  }

  protected runResolver(activatedRoute: ActivatedRoute, params: object): Promise<boolean> {
    const currentRouteParams = _.extend(_.clone(activatedRoute.snapshot.params), params);
    if (this.configuration.keepQueryParams) {
      return this.router.navigate(
        this.routeConfiguration.buildNavigation(currentRouteParams),
        {queryParams: activatedRoute.snapshot.queryParams}
      );
    } else {
      return this.router.navigate(this.routeConfiguration.buildNavigation(currentRouteParams))
    }
  }

  private static generateDefaultConfiguration(): IEntityResolverConfiguration {
    return {
      keepQueryParams: false
    };
  }

}
