import {OnDestroy} from '@angular/core';
import {finalize, takeUntil} from 'rxjs/operators';
import {Observable, Subject, Subscription} from "rxjs";
import {ISubscriptionHandler} from "../../interface/util/i-subscription-handler";
import {SubscriptionManager} from "../../impl/util/subscription-manager";


/** Abstracte Klasse von der alle Komponenten und Services erben. Sie stellt eine Speicherverwaltung fr Observables bereit. */
export abstract class BaseObject implements OnDestroy {
  /** Wird benutzt um Observables bei der Zerstrung einer Komponente auszulsen.
   * Wird im ngOnDestroy ausgelst.
   */
  protected $unsubscribe: Subject<void> = new Subject<void>();
  /** Funktion zur dauerhaften berwachung von Subscriptions. Speicher wird bei Zerstrung der Komponente freigegeben. */
  protected watch<T>(
    observable: Observable<T>,
    handler: ISubscriptionHandler
  ): Subscription {
    return observable
      .pipe(
        takeUntil(this.$unsubscribe),
        finalize( (...args: any[]) => {
          if (handler.onFinished) {
            handler.onFinished(...args);
          }
        })
      )
      .subscribe(
        (...args: any[]) => {
          if (handler.onSuccess) {
            handler.onSuccess(...args);
          }
        },
        (...args: any[]) => {
          if (handler.onError) {
            handler.onError(...args);
          }
        }
      );
  }
  /** Proxy fr SubscriptionManager.subscribe */
  protected subscribe<T>(
    observable: Observable<T>,
    handler: ISubscriptionHandler,
  ): Subscription {
    return SubscriptionManager.subscribe<T>(observable, handler);
  }
  /** Proxy fr SubscriptionManager.subscribeAs */
  protected subscribeAs<T>(
    name: string,
    observable: Observable<T>,
    handler: ISubscriptionHandler
  ): Subscription {
    return SubscriptionManager.subscribeAs<T>(name, observable, handler);
  }
  /** Alle Observables die mit watch berwacht werden, werden hier ausgelst. Angular kmmert sich im den Aufruf.
   *  Falls das Kind auch das Interface 'OnDestroy' implementiert, nicht vergessen super.ngOnDestroy() auszurufen, oder der Speicher
   *  wird nicht freigegeben.
   */
  public ngOnDestroy(): void {
    this.$unsubscribe.next();
    this.$unsubscribe.complete();
  }

}
