import {ReadonlyRestServiceObserver} from './readonly-rest-service-observer';
import {Subject} from "rxjs";


export class RestServiceObserver<T> extends ReadonlyRestServiceObserver<T> {

  public readonly onItemCreated: Subject<T> = new Subject();
  public readonly onItemUpdated: Subject<T> = new Subject();
  public readonly onItemDestroyed: Subject<T> = new Subject();
  public readonly onItemPartialUpdated: Subject<T> = new Subject();
  public readonly onItemSaved: Subject<T> = new Subject();

  private isCreatingItem$: boolean;
  private isUpdatingItem$: boolean;
  private isDestoyingItem$: boolean;
  private isPartiallyUpdatingItem$: boolean;
  private isSavingItem$: boolean;

  constructor() {
    super();
    this.isCreatingItem$ = false;
    this.isUpdatingItem$ = false;
    this.isDestoyingItem$ = false;
    this.isPartiallyUpdatingItem$ = false;
  }

  public isActivate(): boolean {
    return super.isActivate() &&
      !this.isCreatingItem$ &&
      !this.isUpdatingItem$ &&
      !this.isDestoyingItem$ &&
      !this.isPartiallyUpdatingItem$ &&
      !this.isSavingItem$;
  }

  set isCreatingItem(isCreating: boolean) {
    this.isCreatingItem$ = isCreating;
  }

  get isCreatingItem(): boolean {
    return this.isCreatingItem$;
  }

  set isUpdatingItem(isUpdating: boolean) {
    this.isUpdatingItem$ = isUpdating;
  }

  get isUpdatingItem(): boolean {
    return this.isUpdatingItem$;
  }

  set isDestoyingItem(isDestroying: boolean) {
    this.isDestoyingItem$ = isDestroying;
  }

  get isDestoyingItem(): boolean {
    return this.isDestoyingItem$;
  }

  set isPartiallyUpdatingItem(isUpdating: boolean) {
    this.isPartiallyUpdatingItem$ = isUpdating;
  }

  get isPartiallyUpdatingItem(): boolean {
    return this.isPartiallyUpdatingItem$;
  }

  set isSavingItem(isSaving: boolean) {
    this.isSavingItem$ = isSaving;
  }

  get isSavingItem(): boolean {
    return this.isSavingItem$;
  }


}
