import {IDeactivate} from "../../interface/util/i-deactivate";
import { Observable } from 'rxjs';
import { OnDestroy } from '@angular/core';
import {IEntitySelector} from "../../interface/ui/i-entity-selector";
import {SubscriptionHandler} from "../../impl/util/subscription-handler";
import * as _ from 'underscore';
import {AbstractEntityResolver} from "../util/abstract-entity-resolver";
import { ActivatedRoute } from '@angular/router';
import {BasePushStrategyObject} from "./base-push-strategy-object";

export abstract class BaseRootComponent extends BasePushStrategyObject implements IDeactivate, OnDestroy {

  public abstract canDeactivate(): Observable<boolean>;
  protected abstract activatedRoute: ActivatedRoute;
  protected observables: IEntitySelector<any>[] = [];

  public listenTo(observable: IEntitySelector<any>, resolver: AbstractEntityResolver<any, any>): void {
    this.observables.push(observable);
    observable.isVisible.value = true;
    this.watch(observable.nextRequested, new SubscriptionHandler((dataSet: any) => {
      this.onNextRequested(observable, dataSet, resolver);
    }));
  }

  public ngOnDestroy(): void {
    super.ngOnDestroy();
    _.each(this.observables, (observable: IEntitySelector<any>) => observable.isVisible.value = false);
  }

  protected onNextRequested(
    observable: IEntitySelector<any>,
    dataSet: any,
    resolver: AbstractEntityResolver<any, any>
  ): void {
    this.subscribe(this.canDeactivate(), new SubscriptionHandler((canDeactivate: boolean) => {
      if (canDeactivate) {
        // Maybe catch error
        resolver.resolve(this.activatedRoute, dataSet, observable);
      }
    }));
  }

}
