import {IValidator} from '../../interface/validation/i-validator';
import {FormControl} from '@angular/forms';
import * as _ from 'underscore';
import {ValidatorRequired} from '../../impl/validation/validator-required';
import {IAccessableFormControl} from '../../interface/validation/i-accessable-form-control';
import { Observable, Observer } from 'rxjs';


export class AccessableFormControl<TType = any>
  implements IAccessableFormControl<AccessableFormControl<TType>, TType> {

  public control: FormControl;
  public showAs: (value: any) => any;
  public convertTo: (value: any) => any;
  private required$: boolean;
  private validators$: IValidator[];
  private blocked$: boolean;

  public initialize(
    formState?: any,
    validators?: IValidator[]
  ) {
    this.control = new FormControl(formState, null, null);
    this.validateWith(validators);
    this.setValue(formState);
  }

  public validateWith(validators: IValidator[] = []): AccessableFormControl {
    this.validators$ = validators;
    this.control.setValidators(_.map(validators, (v: IValidator) => {
      return v.validator();
    }));
    this.required$ = false;
    _.each(this.validators$, (v: IValidator) => {
      if (v instanceof ValidatorRequired) {
        this.required$ = true;
      }
    });
    this.control.updateValueAndValidity();
    return this;
  }

  public setValue(value: any): AccessableFormControl {
    if (this.showAs) {
      this.control.setValue(this.showAs(value));
    } else {
      this.control.setValue(value);
    }
    return this;
  }

  public reset(value: TType): AccessableFormControl {
    if (this.showAs) {
      this.control.reset(this.showAs(value));
    } else {
      this.control.reset(value);
    }
    return this;
  }

  public markAsUsed(): AccessableFormControl {
    this.control.markAsDirty();
    this.control.markAsTouched();
    this.control.updateValueAndValidity();
    return this;
  }

  public markAsUnused(): AccessableFormControl {
    _.each(this.validators$, (validator: IValidator) => validator.hasError = false );
    this.control.updateValueAndValidity();
    this.control.markAsPristine();
    this.control.markAsUntouched();
    return this;
  }

  public markAsDirty(): AccessableFormControl {
    this.control.markAsDirty();
    return this;
  }

  public markAsTouched(): AccessableFormControl {
    this.control.markAsTouched();
    return this;
  }

  public disable(): AccessableFormControl {
    this.control.disable();
    return this;
  }

  public enable(): AccessableFormControl {
    this.control.enable();
    return this;
  }

  public blockControls(): void {
    this.blocked$ = true;
  }

  public unblockControls(): void {
    this.blocked$ = false;
  }

  get disabled(): boolean {
    return this.control.disabled;
  }

  get valid(): boolean {
    if (this.control.disabled) {
      return true;
    }
    return !this.error;
  }

  get validators(): IValidator[] {
    return this.validators$;
  }

  get error(): IValidator {
    return _.find(this.validators$, (v: IValidator) => v.hasError );
  }

  get value(): any {
    if (this.convertTo) {
      return this.convertTo(this.control.value);
    } else {
      return this.control.value;
    }
  }

  set value(value: any) {
    this.setValue(value);
  }

  get valueChanges(): Observable<any> {
    return Observable.create( (observer: Observer<any>) => {
      const sub = this.control.valueChanges
        .subscribe(
          () => {
            if (!this.blocked) {
              observer.next(this.value);
            }
          },
          null,
          () => {
            sub.unsubscribe();
          }
        );
    });
  }

  get required(): boolean {
    return this.required$;
  }

  get touched(): boolean {
    return this.control.touched;
  }

  get dirty(): boolean {
    return this.control.dirty;
  }

  get blocked(): boolean {
    return this.blocked$;
  }

}
