import {Observable} from 'rxjs';
import {finalize, tap} from 'rxjs/operators';
import {HttpClient} from '@angular/common/http';
import {IWriteableRestService} from "../../interface/http/i-writeable-rest-service";
import {RestServiceObserver} from "./rest-service-observer";
import {Util} from "../util/util";


export class RestHandler<T> implements IWriteableRestService<T> {

  constructor(
    public readonly http: HttpClient,
    public readonly url: string,
    public readonly observer: RestServiceObserver<T>,
    public readonly identifier: string
  ) {}

  public create(model: T, getParams?: object, httpOptions?: object): Observable<T> {
    this.observer.isCreatingItem = true;
    return this.http.post<T>(
        Util.armUrlWithSearchParams(this.url, getParams), model, httpOptions)
      .pipe(
        tap((result: T) => {
          this.observer.onItemCreated.next(result);
        }),
        finalize(() => {
          this.observer.isCreatingItem = false;
        })
      );
  }

  public destroy(id: any, getParams?: object, httpOptions?: object): Observable<void> {
    this.observer.isDestoyingItem = true;
    return this.http.delete<void>(
      Util.armUrlWithSearchParams(
        Util.createEntityUrl(this.url, id), getParams
      ),
      httpOptions
    ).pipe(
      tap(() => {
        this.observer.onItemDestroyed.next(id);
      }),
      finalize(() => {
        this.observer.isDestoyingItem = false;
      })
    );
  }

  public partialUpdate(model: T, getParams?: object, httpOptions?: object): Observable<T> {
    this.observer.isPartiallyUpdatingItem = true;
    return this.http.patch<T>(
      Util.armUrlWithSearchParams(
        Util.createEntityUrl(this.url, model[this.identifier]), getParams
      ),
      model,
      httpOptions
    ).pipe(
      tap((result: T) => {
        this.observer.onItemPartialUpdated.next(result);
      }),
      finalize(() => {
        this.observer.isPartiallyUpdatingItem = false;
      })
    );
  }

  public save(model: T, getParams?: object, httpOptions?: object): Observable<T> {
    this.observer.isSavingItem = true;
    let observable: Observable<T>;
    if (Util.isDefined(model[this.identifier])) {
      observable = this.update(model, getParams, httpOptions);
    } else {
      observable = this.create(model, getParams, httpOptions);
    }
    return observable
      .pipe(
        tap((result: T) => {
          this.observer.onItemSaved.next(result);
        }),
        finalize(() => {
          this.observer.isSavingItem = false;
        })
      );
  }

  public set(model: T, getParams?: object, httpOptions?: object): Observable<T> {
    this.observer.isSavingItem = true;
    let observable: Observable<T>;
    this.observer.isUpdatingItem = true;
    observable = this.http.put<T>(
      Util.armUrlWithSearchParams(
        this.url, getParams
      ),
      model,
      httpOptions
    ).pipe(
      tap((result: T) => {
        this.observer.onItemUpdated.next(result);
      }),
      finalize(() => {
        this.observer.isUpdatingItem = false;
      })
    );

    return observable
      .pipe(
        tap((result: T) => {
          this.observer.onItemSaved.next(result);
        }),
        finalize(() => {
          this.observer.isSavingItem = false;
        })
      );
  }

  public update(model: T, getParams?: object, httpOptions?: object): Observable<T> {
    this.observer.isUpdatingItem = true;
    return this.http.put<T>(
      Util.armUrlWithSearchParams(
        Util.createEntityUrl(this.url, model[this.identifier]), getParams
      ),
      model,
      httpOptions
    ).pipe(
      tap((result: T) => {
        this.observer.onItemUpdated.next(result);
      }),
      finalize(() => {
        this.observer.isUpdatingItem = false;
      })
    );
  }


}
