import {AbstractStoredReadonlyRestservice} from "./abstract-stored-readonly-restservice";
import { Observable, of } from 'rxjs';
import * as _ from 'underscore';
import {TimestampItemStore} from "../../impl/util/timestamp-item-store";

export abstract class AbstractReadonlyCachedRestservice<TModel>
  extends AbstractStoredReadonlyRestservice<TModel> {

  public abstract store: TimestampItemStore<TModel>;

  public all(searchParams?: object, httpOptions?: object): Observable<TModel[]> {
    if (this.store.isStoreValid()) {
      return of(this.store.items);
    } else {
      return super.all(searchParams, httpOptions);
    }
  }

  public show(id: any, getParams?: object, httpOptions?: object): Observable<TModel> {
    if (this.store.isItemValid(id)) {
      const lookup = {} as TModel;
      lookup[this.identifier] = id;
      return of(_.find(this.store.items, lookup));
    }
    return super.show(id, getParams, httpOptions);
  }


}
