import {FormControl, ValidatorFn} from '@angular/forms';
import * as _ from 'underscore';
import * as moment from 'moment';
import {IValidator} from '../../interface/validation/i-validator';


export class ValidatorRequired implements IValidator {

  public hasError = false;
  public error = 'Pflichtfeld';
  private value: any;

  constructor(
    error?: string
  ) {
    if (error) {
      this.error = error;
    }
  }

  public validator(): ValidatorFn {
    return (input: FormControl): object | null => {
      this.value = input.value;
      return this.validate();
    };
  }


  public validate(): any {
    if (_.isDate(this.value)) {
      this.hasError = false;
      return null;
    }
    if (moment.isMoment(this.value)) {
      this.hasError = false;
      return null;
    }
    if (_.isNumber(this.value)) {
      this.hasError = false;
      return null;
    }
    if (_.isString(this.value)) {
      if (this.value.length > 0) {
        this.hasError = false;
        return null;
      } else {
        this.hasError = true;
        return {required: true};
      }
    }
    if (_.isBoolean(this.value)) {
      this.hasError = false;
      return null;
    }
    if (!this.value || _.isEmpty(this.value)) {
      this.hasError = true;
      return {required: true};
    } else {
      this.hasError = false;
      return null;
    }
  }

}
