import {BaseObject} from './base-object';
import {ChangeDetectorRef} from '@angular/core';
import {Observable, Subscription} from 'rxjs';
import {SubscriptionHandler} from '../../impl/util/subscription-handler';


export abstract class BasePushStrategyObject extends BaseObject {

  protected abstract cdr: ChangeDetectorRef;

  protected markForCheckIf(subject: Observable<any>): Subscription {
    return this.watch(subject, new SubscriptionHandler(this.cdr.markForCheck.bind(this.cdr)));
  }

}
