import {FormControl, ValidatorFn} from '@angular/forms';
import {IValidator} from '../../interface/validation/i-validator';
import {REGEX} from '../../enum/regex';


export class ValidatorEmail implements IValidator {

  public hasError = false;
  public error = 'Bitte geben Sie eine gltige E-Mail ein';
  private value: any;

  constructor(
    error?: string
  ) {
    if (error) {
      this.error = error;
    }
  }

  public validator(): ValidatorFn {
    return (input: FormControl) => {
      this.value = input.value;
      return this.validate();
    };
  }

  public validate(): any {
    if (!this.value) {
      return null;
    }
    if (this.value && REGEX.EMAIL.test(this.value.toString())) {
      this.hasError = false;
      return null;
    } else {
      this.hasError = true;
      return {invalidEmail: true};
    }
  }

}
