import {Observable} from 'rxjs';
import {finalize, tap} from 'rxjs/operators';
import {HttpClient} from '@angular/common/http';
import {IReadonlyRestService} from "../../interface/http/i-readonly-restservice";
import {ReadonlyRestServiceObserver} from "./readonly-rest-service-observer";
import {Util} from "../util/util";


export class ReadonlyRestHandler<T> implements IReadonlyRestService<T> {

  identifier: string;

  constructor(
    public readonly http: HttpClient,
    public readonly url: string,
    public readonly observer: ReadonlyRestServiceObserver<T>
  ) {}

  public all(searchParams?: object, httpOptions?: object): Observable<T[]> {
    this.observer.isFetchingItems = true;
    return this.http.get<T[]>(Util.armUrlWithSearchParams(this.url, searchParams), httpOptions)
      .pipe(
        tap( (result: T[]): void => {
          this.observer.onItemsFetched.next(result);
        }),
        // catchError( _.bind(this.serviceErrorHandler.catchError, this.serviceErrorHandler) as (error: any) => Observable<any> ),
        finalize( (): void => {
          this.observer.isFetchingItems = false;
        })
      );
  }

  public show(id: any, getParams?: object, httpOptions?: object): Observable<T> {
    this.observer.isFetchingItem = true;
    return this.http.get<T>(
        Util.armUrlWithSearchParams(Util.createEntityUrl(this.url, id), getParams), httpOptions)
      .pipe(
        tap( (result: T): void => {
          this.observer.onItemFetched.next(result);
        }),
        finalize( (): void => {
          this.observer.isFetchingItem = false;
        })
      );
  }


}
