import {IValidator} from '../../interface/validation/i-validator';
import {REGEX} from '../../enum/regex';
import {FormControl, ValidatorFn} from '@angular/forms';


export class ValidatorInteger implements IValidator {

  public error = 'Bitte geben Sie eine ganze Zahl ein';
  public hasError: boolean;

  private value$: string;
  private regex$: RegExp;


  constructor(
    private allowNegativeValues$: boolean = false
  ) {
    if (allowNegativeValues$) {
      this.regex$ = REGEX.SIGNED_INTEGER;
    } else {
      this.regex$ = REGEX.INTEGER;
    }
  }

  public validate(): any {
    this.hasError = false;
    if (!this.value$) {
      return null;
    }
    if (!this.regex$.test(this.value$)) {
      this.hasError = true;
      return {invalidInteger: true};
    }
    return null;
  }

  public validator(): ValidatorFn {
    return (input: FormControl) => {
      this.value$ = input.value;
      return this.validate();
    };
  }

}
