import {ItemStore} from "./item-store";
import {Util} from "./util";
import * as _ from 'underscore';

export class TimestampItemStore<TModel> extends ItemStore<TModel> {

  public static readonly ITEM_TIMESTAMP_KEY = '__timestamp__';

  private wasFilled: boolean;

  constructor(
    identifier: any,
    private itemLifetimeInSeconds: number,
    transform?: (items: TModel[]) => TModel[]
  ) {
    super(identifier, transform);
    this.wasFilled = false;
  }

  public updateItems(items: TModel[]): void {
    super.updateItems(items);
    _.each(this.items, (item) => this.trackItem(item));
    this.wasFilled = true;
  }

  public clear(): void {
    super.clear();
    this.wasFilled = false;
  }

  public update(item: TModel): void {
    super.update(item);
    this.trackItem(item);
  }

  public push(item: TModel): void {
    super.push(item);
    this.trackItem(item);
  }

  public isItemValid(identifier: any): boolean {
    let itemInStore = null;
    for(const item of this.items) {
      if (item[this.identifier] === identifier) {
        itemInStore = item;
        break;
      }
    }
    if (!Util.isDefined(itemInStore)) {
      return false;
    }
    const currentTimeStamp = Util.getCurrentUnixTimestamp();
    return (currentTimeStamp - this.itemLifetimeInSeconds) < itemInStore[TimestampItemStore.ITEM_TIMESTAMP_KEY];
  }

  public isStoreValid(): boolean {
    if (!this.wasFilled) {
      return false;
    }
    if (_.isEmpty(this.items)) {
      return false;
    }
    return _.all(this.items, (item) => this.isItemValid(item[this.identifier]));
  }

  private trackItem(item: TModel): void {
    item[TimestampItemStore.ITEM_TIMESTAMP_KEY] = Util.getCurrentUnixTimestamp();
  }

}
