import {EventEmitter} from '@angular/core';
import {Subject} from "rxjs";


export class ReadonlyRestServiceObserver<T> {

  public readonly onItemFetched: Subject<T> = new Subject();
  public readonly onItemsFetched: Subject<T[]> = new Subject();

  private isFetchingItem$: boolean;
  private isFetchingItems$: boolean;

  constructor() {
    this.isFetchingItem$ = false;
    this.isFetchingItems$ = false;
  }

  public isActivate(): boolean {
    return !this.isFetchingItem && !this.isFetchingItems;
  }

  set isFetchingItems(fetching: boolean) {
    this.isFetchingItems$ = fetching;
  }

  get isFetchingItems(): boolean {
    return this.isFetchingItems$;
  }

  set isFetchingItem(fetching: boolean) {
    this.isFetchingItem$ = fetching;
  }

  get isFetchingItem(): boolean {
    return this.isFetchingItem$;
  }
}
