import { Resolve, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs';
import {AbstractRouteConfiguration} from "./abstract-route-configuration";
import {IRoutingData} from "../../interface/routing/i-routing-configuration";

export abstract class AbstractResolver<TData, TRouteParams> implements Resolve<TData> {

  protected route: ActivatedRouteSnapshot;
  protected state: RouterStateSnapshot;

  protected abstract onResolve(): Observable<TData>;

  public resolve(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot
  ): Observable<TData> | Promise<TData> | TData {
    this.route = route;
    this.state = state;
    return this.onResolve();
  }

  protected get routeConfiguration(): AbstractRouteConfiguration<TRouteParams> {
    return (this.route.data as IRoutingData<TRouteParams>).routeConfiguration;
  }

}
