import {IDeactivate} from "../../interface/util/i-deactivate";
import { Observable, Subscriber } from 'rxjs';
import {SubscriptionManager} from "./subscription-manager";
import {SubscriptionHandler} from "./subscription-handler";

export class DeactivationHandler implements IDeactivate {

  private deactivators$: { [name: string]: IDeactivate } = {};


  public register(name: string, component: IDeactivate): void {
    this.deactivators$[name] = component;
  }

  public remove(name: string): void {
    delete this.deactivators$[name];
  }

  public canDeactivate(): Observable<boolean> {
    return new Observable<boolean>(subscriber => {
      this._deactivateComponent(0, subscriber);
    });
  }

  private _deactivateComponent(index: number, observer: Subscriber<boolean>): void {
    const component = Object.values(this.deactivators$)[index];
    if (!component) {
      observer.next(true);
      observer.complete();
    } else {
      SubscriptionManager.subscribe(component.canDeactivate(), new SubscriptionHandler( (canDeactivate: boolean) => {
        if (!canDeactivate) {
          observer.next(false);
          observer.complete();
        } else {
          this._deactivateComponent(index + 1, observer);
        }
      }));
    }
  }

}
