import {AbstractStoredReadonlyRestservice} from "./abstract-stored-readonly-restservice";
import {IWriteableRestService} from "../../interface/http/i-writeable-rest-service";
import {RestServiceObserver} from "../../impl/http/rest-service-observer";
import {RestHandler} from "../../impl/http/rest-handler";
import { Observable } from 'rxjs';
import { tap } from 'rxjs/operators';
import {Util} from "../../impl/util/util";

export abstract class AbstractStoredRestservice<TModel>
  extends AbstractStoredReadonlyRestservice<TModel>
  implements IWriteableRestService<TModel> {

  public readonly observer: RestServiceObserver<TModel>;

  protected constructor() {
    super(new RestServiceObserver<TModel>());
  }

  public create(model: TModel, getParams?: object, httpOptions?: object): Observable<TModel> {
    let request = new RestHandler(this.http, this.url, this.observer, this.identifier)
      .create(model, getParams, httpOptions)
    return this.modifyCreateCall(request);
  }

  public destroy(id: any, getParams?: object, httpOptions?: object): Observable<void> {
    return new RestHandler(this.http, this.url, this.observer, this.identifier)
      .destroy(id, getParams, httpOptions)
      .pipe(
        tap( () => {
          this.store.removeByIdentifier(id);
        })
      );
  }

  public partialUpdate(model: TModel, getParams?: object, httpOptions?: object): Observable<TModel> {
    return new RestHandler(this.http, this.url, this.observer, this.identifier)
      .partialUpdate(model, getParams, httpOptions)
      .pipe(
        tap( (result: TModel) => {
          this.store.update(result);
        })
      );
  }

  public save(model: TModel, getParams?: object, httpOptions?: object): Observable<TModel> {
    let request = new RestHandler(this.http, this.url, this.observer, this.identifier)
      .save(model, getParams, httpOptions);
    if (Util.isDefined(model[this.identifier])) {
      request = this.modifyUpdateCall(request);
    } else {
      request = this.modifyCreateCall(request);
    }
    return request;
  }

  public set(model: TModel, getParams?: object, httpOptions?: object): Observable<TModel> {
    let request = new RestHandler(this.http, this.url, this.observer, this.identifier)
      .set(model, getParams, httpOptions);
    if (Util.isDefined(model[this.identifier])) {
      request = this.modifyUpdateCall(request);
    } else {
      request = this.modifyCreateCall(request);
    }
    return request;
  }

  public update(model: TModel, getParams?: object, httpOptions?: object): Observable<TModel> {
    let request = new RestHandler(this.http, this.url, this.observer, this.identifier)
      .update(model, getParams, httpOptions);
    request = this.modifyUpdateCall(request);
    return request;
  }

  protected modifyCreateCall(observable: Observable<TModel>): Observable<TModel> {
    return observable.pipe(
      tap( (result: TModel) => {
        this.store.push(result);
      })
    );
  }

  protected modifyUpdateCall(observable: Observable<TModel>): Observable<TModel> {
    return observable.pipe(
      tap( (result: TModel) => {
        this.store.update(result);
      })
    );
  }

}
