import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, UrlTree } from '@angular/router';
import { Observable, Subscriber, forkJoin } from 'rxjs';
import * as _ from 'underscore';
import {Util} from "../../impl/util/util";
import {SubscriptionManager} from "../../impl/util/subscription-manager";
import {SubscriptionHandler} from "../../impl/util/subscription-handler";

export abstract class AbstractActivationQueuedGuard implements CanActivate {

  private readonly queue: CanActivate[][] = [];

  private route: ActivatedRouteSnapshot;
  private state: RouterStateSnapshot;

  public canActivate(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot
  ): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
    this.route = route;
    this.state = state;
    return new Observable((subscriber) => {
      this.runQueueRecursive(0, subscriber);
    });
  }

  protected sequence(step: CanActivate[] | CanActivate): AbstractActivationQueuedGuard {
    if (_.isArray(step)) {
      this.queue.push(step as CanActivate[]);
    } else {
      this.queue.push([step as CanActivate]);
    }
    return this;
  }

  private runQueueRecursive(level: number, subscriber: Subscriber<boolean | UrlTree>): void {
    const currentQueueItems: CanActivate[] = this.queue[level];
    if (!Util.isDefined(currentQueueItems)) {
      subscriber.next(true);
      subscriber.complete();
      return;
    }
    const activators: CanActivate[] = _.map(currentQueueItems, (queueItem: CanActivate) => {
      return queueItem.canActivate(this.route, this.state);
    });
    SubscriptionManager.subscribe(forkJoin(activators), new SubscriptionHandler(
      (canActivateStates: boolean[]) => {
        if (!_.all(canActivateStates)) {
          subscriber.next(false);
          subscriber.complete();
        } else {
          this.runQueueRecursive(level + 1, subscriber);
        }
      },
      () => {
        subscriber.next(false);
        subscriber.complete();
      }
    ));
  }

}
