import {BaseObject} from "../ui/base-object";
import {IReadonlyStoreRestService} from "../../interface/http/i-readonly-stored-rest-service";
import {ItemStore} from "../../impl/util/item-store";
import {ReadonlyRestServiceObserver} from "../../impl/http/readonly-rest-service-observer";
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import {ReadonlyRestHandler} from "../../impl/http/readony-rest-handler";
import { tap } from 'rxjs/operators';
import {Util} from "../../impl/util/util";

export abstract class AbstractStoredReadonlyRestservice<TModel> extends BaseObject
  implements IReadonlyStoreRestService<TModel> {

  public abstract url: string;
  public abstract store: ItemStore<TModel>;
  public abstract identifier: string;
  protected abstract http: HttpClient;

  public readonly observer: ReadonlyRestServiceObserver<TModel>;

  protected constructor(
    restServiceObserver?: ReadonlyRestServiceObserver<TModel>
  ) {
    super();
    this.observer = Util.isDefined(restServiceObserver) ? restServiceObserver :
      new ReadonlyRestServiceObserver<TModel>();
  }

  public all(searchParams?: object, httpOptions?: object): Observable<TModel[]> {
    return new ReadonlyRestHandler<TModel>(this.http, this.url, this.observer).all(searchParams, httpOptions)
      .pipe(
        tap( (response: TModel[]) => {
          this.store.updateItems(response);
        })
      );
  }

  public show(id: any, getParams?: object, httpOptions?: object): Observable<TModel> {
    return new ReadonlyRestHandler<TModel>(this.http, this.url, this.observer).show(id, getParams, httpOptions)
      .pipe(
        tap( (response: TModel) => {
          this.store.update(response);
        })
      );
  }


}
