import * as _ from 'underscore';

export class Util {

  public static isDefined(obj: any): boolean {
    return obj !== null && obj !== undefined;
  }

  public static armUrlWithSearchParams(url: string, queryParams: object): string {
    if (!queryParams || _.isEmpty(queryParams)) {
      return url;
    }
    _.each(queryParams, (value: any, key: string) => {
      const querySelector = url.indexOf('?') === -1 ? '?' : '&';
      url = `${url}${querySelector}${key}=${value}`;
    });
    return url;
  }

  public static createEntityUrl(url: string, id: any): string {
    return `${url}/${id}`;
  }

  public static getBrowser(): string {
    const ua = navigator.userAgent;
    let tem;
    let M = ua.match(/(opera|chrome|safari|firefox|msie|trident(?=\/))\/?\s*(\d+)/i) || [];
    if (/trident/i.test(M[1])) {
      tem = /\brv[ :]+(\d+)/g.exec(ua) || [];
      return 'IE ' + (tem[1] || '');
    }
    if (M[1] === 'Chrome') {
      tem = ua.match(/\b(OPR|Edge)\/(\d+)/);
      if (tem != null) {
        return tem.slice(1).join(' ').replace('OPR', 'Opera');
      }
    }
    M = M[2] ? [M[1], M[2]] : [navigator.appName, navigator.appVersion, '-?'];
    tem = ua.match(/version\/(\d+)/i);
    if (tem != null) {
      M.splice(1, 1, tem[1]);
    }
    return M.join(' ');
  }

  public static getCurrentUnixTimestamp(): number {
    return Math.round((new Date()).getTime() / 1000);
  }

  public static guid(): string {
    const s4 = () => {
      return Math.floor((1 + Math.random()) * 0x10000)
        .toString(16)
        .substring(1);
    };
    return s4() + s4() + '-' + s4() + '-' + s4() + '-' + s4() + '-' + s4() + s4() + s4();
  }

  private static transformUmlaute(value: string): string {
    return value
      .replace('', 'Ae')
      .replace('', 'ae')
      .replace('', 'Oe')
      .replace('', 'oe')
      .replace('', 'Ue')
      .replace('', 'ue');
  }

  public static sortAlpabetically<TListType>(list: TListType[], attribute: string): TListType[] {
    const resultList = _.sortBy(list, (item: TListType) => this.transformUmlaute(item[attribute]).toUpperCase());
    return resultList;
  }

}
