import { IAccessableFormControl } from '../../interface/validation/i-accessable-form-control';
import { FormArray } from '@angular/forms';
import { Type } from '@angular/core';
import * as _ from 'underscore';
import { Util } from '../../impl/util/util';
import { IValidator } from '../../interface/validation/i-validator';
import { ValidatorRequired } from '../../impl/validation/validator-required';

export abstract class AccessableFormArray<TModel>
  implements IAccessableFormControl<AccessableFormArray<TModel>, TModel[]> {

  private validators$: IValidator[] = [];
  public control: FormArray;
  public type: Type<IAccessableFormControl<any, any>>;
  private required$: boolean = false;

  private entries$: IAccessableFormControl<any, TModel>[];

  public initialize(type: Type<IAccessableFormControl<any, any>>): void {
    this.control = new FormArray([]);
    this.type = type;
    this.entries$ = [];
  }

  public push(entry: TModel): IAccessableFormControl<any, TModel> {
    const item = this.generateFormEntry(entry);
    this.control.push(item.control);
    this.entries$.push(item);
    return item;
  }

  public removeAt(index: number): void {
    this.control.removeAt(index);
    this.entries$ = _.reject(this.entries$, (e, round: number) => {
      return round === index;
    });
    let markAsUsed = false;
    for (const entry of this.entries$) {
      markAsUsed = markAsUsed || entry.dirty;
    }
    markAsUsed ? this.markAsUsed() : this.markAsUnused();
  }

  public validateWith(validators: IValidator[]): AccessableFormArray<TModel> {
    if (!Util.isDefined(validators)) {
      validators = [];
    }
    if (_.isArray(validators)) {
      this.validators$ = validators;
    }
    this.control.setValidators(_.map(validators, (v: IValidator) => v.validator()));
    this.required$ = false;
    _.each(this.validators$, (v: IValidator) => {
      if (v instanceof ValidatorRequired) {
        this.required$ = true;
      }
    });
    this.control.updateValueAndValidity();
    return this;
  }

  public clearValidators(): void {
    this.validators$ = [];
  }

  public generateFormEntry(value: any): IAccessableFormControl<any, TModel> {
    const validation = new this.type();
    validation.reset(value);
    return validation;
  }

  public get errors(): string[] {
    return _.compact(_.map(this.validators$, (e: IValidator) => {
      if (e.hasError) {
        return e.error;
      }
      return null;
    }));
  }

  public get error(): IValidator {
    return _.find(this.validators$, (v) => {
      return v.hasError;
    });
  }

  public get validators(): IValidator[] {
    return this.validators$;
  }

  get required(): boolean {
    return this.required$;
  }

  get dirty(): boolean {
    return this.control.dirty;
  }

  get touched(): boolean {
    return this.control.touched;
  }

  get valid(): boolean {
    return this.control.valid;
  }

  get value(): TModel[] {
    const ret: TModel[] = [];
    for (const entry of this.entries$) {
      ret.push(entry.value);
    }
    return ret;
  }

  get entries(): IAccessableFormControl<any, TModel>[] {
    return this.entries$;
  }

  public disable(): AccessableFormArray<TModel> {
    this.control.disable();
    return this;
  }

  public enable(): AccessableFormArray<TModel> {
    this.control.enable();
    for (const entry of this.entries$) {
      entry.enable();
    }
    return this;
  }

  public markAsDirty(): AccessableFormArray<TModel> {
    this.control.markAsDirty();
    for (const entry of this.entries$) {
      entry.markAsDirty();
    }
    return this;
  }

  public markAsTouched(): AccessableFormArray<TModel> {
    this.control.markAsTouched();
    for (const entry of this.entries$) {
      entry.markAsTouched();
    }
    return this;
  }

  public markAsUnused(): AccessableFormArray<TModel> {
    this.control.markAsUntouched();
    this.control.markAsPristine();
    for (const entry of this.entries$) {
      entry.markAsUnused();
    }
    return this;
  }

  public markAsUsed(): AccessableFormArray<TModel> {
    this.control.markAsTouched();
    this.control.markAsDirty();
    for (const entry of this.entries$) {
      entry.markAsUsed();
    }
    return this;
  }

  public reset(models: TModel[]): AccessableFormArray<TModel> {
    this.entries$ = [];
    this.control.reset([]);
    if (Util.isDefined(models) && _.isArray(models)) {
      for (const model of models) {
        this.push(model);
      }
    }

    return this;
  }

}
