import {IValidator} from '../../interface/validation/i-validator';
import {AccessableFormControl} from '../../abstract/validation/accessable-form-control';
import {TypeConverter} from '../util/type-converter';
import {Util} from '../util/util';

export abstract class AccessableControlFactory {

  public static simpleControl(
    formState: any = null,
    validators: IValidator[]
  ): AccessableFormControl {
    const control = new AccessableFormControl();
    control.initialize(formState, validators);
    return control;
  }

  public static numberControl(
    formState: any = null,
    validators: IValidator[]
  ): AccessableFormControl {
    const control = AccessableControlFactory.simpleControl(formState, validators);
    control.showAs = TypeConverter.asGermanFloat;
    control.convertTo = TypeConverter.toNumber;
    control.setValue(formState);
    return control;
  }

  public static numberBooleanControl(
    formState: any = null,
    validators: IValidator[]
  ): AccessableFormControl {
    const control = AccessableControlFactory.simpleControl(formState, validators);
    control.showAs = TypeConverter.numberToBoolean;
    control.convertTo = TypeConverter.booleanToNumber;
    control.setValue(formState);
    return control;
  }

  public static dateControl(
    formState: any = null,
    validators: IValidator[]
  ): AccessableFormControl {
    const control = AccessableControlFactory.simpleControl(formState, validators);
    return control;
  }

  /** Schneides alle Whitespaces am Ende und Anfang weg */
  public static trimControl(
    formState: any = null,
    validators: IValidator[]
  ): AccessableFormControl {
    const control = AccessableControlFactory.simpleControl(formState, validators);
    control.showAs = (str: string): string | undefined | null => {
      if (Util.isDefined(str)) {
        return str.trim();
      }
      return str;
    };
    control.convertTo = (str: string): string | undefined | null => {
      if (Util.isDefined(str)) {
        return str.trim();
      }
      return str;
    };
    return control;
  }
}
