import {finalize, take} from 'rxjs/operators';
import {Observable, Subscription} from "rxjs";
import {ISubscriptionHandler} from "../../interface/util/i-subscription-handler";
import * as _ from 'underscore';

/** Diese Klasse kmmert sich um die Speicherverwaltung von Subscriptions.
 Diese erzeugen Memory-Leaks, wenn sie nicht sauber gelscht werden. */
export abstract class SubscriptionManager {
  // Alle benannten Subscriptions werden hier gecached
  private static subscriptions: object = {};
  /** Subscription mit einem Namen. Diese Funktion ist ntzlich,
   falls eine Subscription fters aufgerufen wird und die vorherige, falls
   noch nicht abgeschlossen, beendet werden soll (HTTP Aufrufe). */
  public static subscribeAs<T>(
    name: string,
    observable: Observable<T>,
    handler: ISubscriptionHandler
  ): Subscription {
    const sub = observable
      .pipe(
        take(1),
        finalize((...args: any[]) => {
          if (_.isFunction(handler.onFinished)) {
            handler.onFinished(...args);
            if (sub) {
              this.deleteSubscription(name);
            }
          }
        })
      )
      .subscribe(
        (...args: any[]) => {
          if (_.isFunction(handler.onSuccess)) {
            handler.onSuccess(...args);
          }
        },
        (...args: any[]) => {
          if (_.isFunction(handler.onError)) {
            handler.onError(...args);
          }
        }
      );
    this.pushSubscription(name, sub);
    return sub;
  }
  /** Funktion fr einmalige Subscriptions (z.B.: Initialisierungsaufrufe) */
  public static subscribe<T>(
    observable: Observable<T>,
    handler: ISubscriptionHandler
  ): Subscription {
    const sub = observable
      .pipe(
        take(1),
        finalize((...args: any[]) => {
          if (_.isFunction(handler.onFinished)) {
            handler.onFinished(...args);
            if (sub) {
              this.deleteSubscription(name);
            }
          }
        })
      )
      .subscribe(
        (...args: any[]) => {
          if (_.isFunction(handler.onSuccess)) {
            handler.onSuccess(...args);
          }
        },
        (...args: any[]) => {
          if (_.isFunction(handler.onError)) {
            handler.onError(...args);
          }
        }
      );
    return sub;
  }

  private static pushSubscription(name: string, subscription: Subscription): void {
    this.deleteSubscription(name);
    SubscriptionManager.subscriptions[name] = subscription;
  }

  private static deleteSubscription(name: string): void {
    const sub = SubscriptionManager.subscriptions[name];
    if (sub) {
      sub.unsubscribe();
      delete SubscriptionManager.subscriptions[name];
    }
  }

}

