import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs';
import {BaseObject} from '../ui/base-object';
import {IReadonlyRestService} from '../../interface/http/i-readonly-restservice';
import {ReadonlyRestServiceObserver} from '../../impl/http/readonly-rest-service-observer';
import {ReadonlyRestHandler} from '../../impl/http/readony-rest-handler';
import {Util} from "../../impl/util/util";

export abstract class AbstractReadonlyRestservice<T> extends BaseObject implements IReadonlyRestService<T> {

  identifier: string;
  public abstract url: string;
  public readonly observer: ReadonlyRestServiceObserver<T>;
  protected abstract http: HttpClient;

  protected constructor(
    restServiceObserver?: ReadonlyRestServiceObserver<T>
  ) {
    super();
    this.observer = Util.isDefined(restServiceObserver) ? restServiceObserver :
      new ReadonlyRestServiceObserver<T>();
  }

  public all(searchParams?: object, httpOptions?: object): Observable<T[]> {
    return new ReadonlyRestHandler<T>(this.http, this.url, this.observer).all(searchParams, httpOptions);
  }

  public show(id: any, getParams?: object, httpOptions?: object): Observable<T> {
    return new ReadonlyRestHandler<T>(this.http, this.url, this.observer).show(id, getParams, httpOptions);
  }

}
