import { Subject } from 'rxjs';
import {Util} from "./util";

/**
 * Diese Klasse kann verwendet werden um Werte zu speichern und die automatisch
 * berwachbar zu machen. Dadurch knnen UI Push Strategien leichter implementiert werden.
 */
export class ObservableValue<TValueType> {

  private value$: TValueType;
  public readonly changed: Subject<TValueType> = new Subject<TValueType>();

  public constructor(
    initialValue?: TValueType,
    private readonly equals?: (current: TValueType, next: TValueType) => boolean
  ) {
    this.value$ = initialValue;
    this.equals = Util.isDefined(this.equals) ? this.equals : this.defaultComparator;
  }


  public set value(value: TValueType) {
    if (!this.equals(this.value$, value)) {
      this.value$ = value;
      this.changed.next(value);
    }
  }

  public get value(): TValueType {
    return this.value$;
  }


  private defaultComparator(current: TValueType, next: TValueType): boolean {
    return current === next;
  }

}
