import {Subject} from 'rxjs';
import {Util} from "./util";


export class ItemStore<T> {

  public readonly onChanged: Subject<T[]> = new Subject();

  protected items$: object = {};
  protected transformedItems$: T[] = [];

  constructor(
    public readonly identifier: any,
    public readonly transform?: (items: T[]) => T[]
  ) {}

  public updateItems(items: T[]): void {
    this.clear();
    items = items || [];
    for (const item of items) {
      this.items$[item[this.identifier]] = item;
    }
    this.internalTransform$();
  }

  public clear(): void {
    this.items$ = {};
    this.transformedItems$ = [];
  }

  public remove(item: T): void {
    if (item) {
      delete this.items$[item[this.identifier]];
      this.internalTransform$();
    }
  }

  public removeByIdentifier(identifier: any): void {
    if (Util.isDefined(identifier)) {
      delete this.items$[identifier];
      this.internalTransform$();
    }
  }

  public update(item: T): void {
    if (item) {
      this.items$[item[this.identifier]] = item;
      this.internalTransform$();
    }
  }

  public push(item: T): void {
    if (item) {
      this.items$[item[this.identifier]] = item;
      this.internalTransform$();
    }
  }

  protected internalTransform$(): void {
    this.transformedItems$ = Object.values(this.items$);
    if (this.transform) {
      this.transformedItems$ = this.transform(this.transformedItems$);
    }
    this.onChanged.next(this.transformedItems$);
  }

  get items(): T[] {
    return this.transformedItems$;
  }
}
