import {FormGroup} from '@angular/forms';
import * as _ from 'underscore';
import {IAccessableFormControl} from '../../interface/validation/i-accessable-form-control';
import {Util} from '../../impl/util/util';
import { Subject, Observable } from 'rxjs';

export abstract class AccessableFormGroup<TFields, TModel extends object>
  implements IAccessableFormControl<AccessableFormGroup<TFields, TModel>, TModel> {

  public control: FormGroup;
  private fields$: TFields;
  private changed$: Subject<any> = new Subject();

  public initialize(
    fields: TFields
  ): void {
    this.control = new FormGroup(
      _.mapObject(fields, (field) => {
        return field.control;
      })
    );
    this.fields$ = fields;
  }

  public markAsUsed(): AccessableFormGroup<TFields, TModel> {
    _.each(this.fields$, (field) => field.markAsUsed() );
    return this;
  }

  public markAsUnused(): AccessableFormGroup<TFields, TModel> {
    _.each(this.fields$, (field) => field.markAsUnused() );
    return this;
  }

  public markAsDirty(): AccessableFormGroup<TFields, TModel> {
    _.each(this.fields$, (field) => field.markAsDirty() );
    return this;
  }

  public markAsTouched(): AccessableFormGroup<TFields, TModel> {
    _.each(this.fields$, (field) => field.markAsTouched() );
    return this;
  }

  public reset(model: TModel, propagateChanges: boolean = true): AccessableFormGroup<TFields, TModel> {
    if (!Util.isDefined(model)) {
      model = {} as TModel;
    }
    _.each(this.fields$, (field: IAccessableFormControl<any, any>, key: string) => field.reset(model[key]));

    if (propagateChanges) {
      this.changed$.next();
    }
    return this;
  }

  get fields(): TFields {
    return this.fields$;
  }


  get value(): TModel {
    const ret = {} as TModel;
    _.each(this.fields$, (field: IAccessableFormControl<any, any>, key: string) => ret[key] = field.value);
    return ret;
  }

  get dirty(): boolean {
    return this.control.dirty;
  }

  get valid(): boolean {
    return this.control.valid;
  }

  get touched(): boolean {
    return this.control.touched;
  }

  disable(): AccessableFormGroup<TFields, TModel> {
    this.control.disable();
    return this;
  }

  enable(): AccessableFormGroup<TFields, TModel> {
    this.control.enable();
    return this;
  }

  get disabled(): boolean {
    return this.control.disabled;
  }

  get enabled(): boolean {
    return this.control.enabled;
  }

  get changed(): Observable<void> {
    return this.changed$.asObservable();
  }

}
